import md5 from '../../static/util/md5.js';
import mInput from '@/components/m-input.vue';
import img from '../../static/img/login/Login-bg.png';
import service from '../../static/service/service.js';

export default {
	components: {
		mInput,
		md5
	},
	data() {
		return {
			// 账号
			account: 'admin',
			// 密码
			password: '123456',
			// 为了适配小程序 将顶部背景图转成base64
			background: img
		}
	},
	methods: {
		async handleLoginBtn() {
			// 名称判断
			if (this.account.length < 4 || this.account.length == 0) {
				uni.showToast({
					icon: 'none',
					title: '请输入正确的名称'
				});
				return;
			}

			// 密码判断
			if (this.password.length < 6 || this.password.length == 0) {
				uni.showToast({
					icon: 'none',
					title: '请输入正确的密码'
				});
				return;
			}

			const res = await service.login(this.account, md5(this.password),)
			console.log(res);
			if (res.errno == 0) {
				uni.setStorageSync('token', res.data.token)
				uni.showToast({
					icon: 'none',
					title: '登录成功'
				});
				// setTimeout(function() {
				// 	uni.hideToast(),
				// 		uni.reLaunch({
				// 			url: '../task/task'
				// 		})
				// }, 1500);
			} else {
				uni.showToast({
					icon: 'none',
					title: res.errmsg
				})
			}
		},
		wxGetUserInfo:function(res){
			console.log(res);
			if (!res.detail.iv) {
				uni.showToast({
					title: "您取消了授权,登录失败",
					icon: "none"
				});
				return false;
			}
			console.log('-------用户授权，并获取用户基本信息和加密数据------')
			console.log(res.detail);
		}
	},

	onLoad() {
		let base64 = uni.getFileSystemManager().readFileSync(this.background, 'base64');
		this.background = 'data:image/png;base64,' + base64;
	}
}
